import EventBlock from "../eventBlock/eventBlock"
import EventMap from "../eventMap/eventMap"
import './event.scss'

const EventsNear = () => {
    return (
        <div className="event">
            <div className="container">
                <div className="event__content">
                    <EventBlock title="Мероприятия рядом"/>
                    <EventMap/>
                </div>
            </div>
        </div>
    )
}

export default EventsNear