import {
    Link, Route, Switch
  } from "react-router-dom";
import './eventCard.scss'

import eventImage from'../../../img/design/discoparty.svg'
import ava1 from '../../../img/content/avatar_1.png'
import ava2 from '../../../img/content/avatar_2.png'
import ava3 from '../../../img/content/avatar_3.png'
import ava4 from '../../../img/content/avatar_4.png'

const title = () => {
    return (        
        <Link to="/event">
            <h3 className="event__title">Название</h3>
        </Link>
    )
}

const titleEvent = () => {
    return (
        <h3 className="event__title">Название</h3>
    )
}

const EventCard = (props) => {
    return (
        
        <div className="event__card">
            <div className="event__top">
                <div className="event__left">
                    <img src={eventImage} alt="" className="event__image"/>

                    <div className="event__info">
                        <Switch>
                            <Route exact path="/" component={title} />
                            <Route exact path="/event" component={titleEvent} />
                        </Switch>
                        <span className="event__address"><span className="highlight"> 2км. </span> Ул. Иоана Петра Первого 37</span>
                    </div>
                </div>

                <div className="event__right">
                    <div className="event__rating">4,2</div>
                </div>
            </div>

            <div className="event__main">
                <div className="event__shedule">
                    <div className="event__start">
                        <span className="title">Начало мероприятия</span>
                        <div className="event__data">25.10.2019</div>
                        <div className="event__time">20:00</div>
                    </div>

                    <div className="event__separate"></div>

                    <div className="event__end">
                        <span className="title">Начало мероприятия</span>
                        <div className="event__data">25.10.2019</div>
                        <div className="event__time">20:00</div>
                    </div>
                </div>

                <div className="event__members">
                    <div className="event__counter">
                        <span className="event__ready">24 </span>
                        из
                        <span className="event__total"> 30</span>
                    </div>

                    <span className="title">участники мероприятия</span>

                    <div className="event__avatars">
                        <div className="event__avatar">
                            <img src={ava1} alt="" className="avatar"/>
                        </div>
                        <div className="event__avatar">
                            <img src={ava2} alt="" className="avatar"/>
                        </div>
                        <div className="event__avatar">
                            <img src={ava3} alt="" className="avatar"/>
                        </div>
                        <div className="event__avatar">
                            <img src={ava4} alt="" className="avatar"/>
                        </div>
                        <div className="event__avatar event__avatar--more">
                            20+
                        </div>
                    </div>
                </div>

            </div>
            <div className="event__bottom">
                <span className='event__enter'>Вход на мероприятие</span>
                {props.free 
                    ?   <span className="event__type success">Свободный вход</span>
                    
                    : <span className="event__type event__type--price">300 <span className="currency">RUB</span></span>
                }
            </div>
        </div>
    )
}

export default EventCard