import './banners.scss'
import BannerText from './bannerText'
import img from '../../img/content/banner1.svg'

const BannerFirst = () => {
    return (
        <div className="banner banner--first">
            <div className="container">
                <div className="banner__content">
                    <BannerText title="Здесь ты найдешь чем занятся!"/>

                    <img src={img} alt="" className="banner__img"/>
                </div>
            </div>
        </div>
    )
}

export default BannerFirst