import {
     NavLink
  } from "react-router-dom";

const HeaderNav = ({open, changeOpen}) => {

    return (
        <nav className={open ? 'header__nav active' : 'header__nav'}>
            <ul className="header__list">
                <li className="header__list_item ">
                    <NavLink  exact={true} to="/" activeClassName="active" onClick={() => changeOpen()}>Карта Мероприятий</NavLink >
                </li>
                <li className="header__list_item">
                    <NavLink to="/about" activeClassName="active" onClick={() => changeOpen()}>Об Приложении</NavLink>
                </li>
                <li className="header__list_item">
                    <a href="#">Вход</a>
                </li>
            </ul>
        </nav>
    )
}

export default HeaderNav