import './list.scss'

const List = (props) => {
    return (
        <div className="list_section">
            <div className="container">
                <div className="list_section__content">
                    <h2 className="section_title">{props.title}</h2>

                    <ul className="list_section__block">
                        <li className="list_section__item">Создавать мероприятия</li>
                        <li className="list_section__item">Участвовать в других мероприятиях</li>
                        <li className="list_section__item">Контролировать участников мероприятия</li>
                        <li className="list_section__item">Общаться в общих чатах мероприятия</li>
                        <li className="list_section__item">Делать платные / бесплатные мероприятия</li>
                        <li className="list_section__item">Оставлять отзывы об участниках / организаторах мероприятий</li>
                        <li className="list_section__item">Контролировать зоны поиска мероприятий</li>
                        <li className="list_section__item">Создать свой профиль и поднимать свой рейтинг организатора / участника!</li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default List