import appStore from '../../img/design/appstore.png'
import googlePlay from '../../img/design/googleplay.png'

const BannerText = (props) => {
    return (
        <div className="banner__text">
            <h2 className="section_title">{props.title}</h2>
            <p className="banner__description">Ты можешь создать событие, пойти на него с друзьями, собрать денег на тусовку своей мечты!</p>
            <div className="banner__links">
                <a href="#" className="banner__link">
                    <img src={appStore} alt=""/>
                </a>
                <a href="#" className="banner__link">
                    <img src={googlePlay} alt=""/>
                </a>
            </div>
        </div>
    )
}
export default BannerText