import './eventDetails.scss'
import ava1 from '../../../img/content/avatar_1.png'

const EventDetails = () => {
    return (
        <div className="event__details">
            <div className="event__categories">

                <div className="event__category">
                    <h4 className="subtitle">Категория события:</h4>
                    <div className="event__value">Вечеринки и дискотеки</div>
                </div>

                <div className="event__category">
                    <h4 className="subtitle">Локация события:</h4>
                    <div className="event__value">Квартира организатора</div>
                </div>

                <div className="event__category">
                    <h4 className="subtitle">Дополнительные категории:</h4>
                    <div className="event__value">Музыка, Кино и сериалы</div>
                </div>

                <div className="event__creator">
                   <div className="event__avatar">
                        <img src={ava1} alt=""/>
                    </div>

                    <div className="event__creator_info">
                        <h4 className="subtitle">Создатель события</h4>
                        <div className="event__name">Анна Петрова</div>
                    </div>
                </div>

            </div>

            <div className="event__tags">

                <h4 className="subtitle">Теги события:</h4>

                <ul className="tag__list">
                    <li className="tag tag--primary">бесплатно</li>
                    <li className="tag tag--primary">ДЛЯ ВЗРОСЛЫХ</li>
                    <li className="tag tag--primary">тег ё-моё</li>
                    <li className="tag tag--primary">Алкоголь</li>
                    <li className="tag tag--primary">Можно курить </li>
                    <li className="tag tag--primary">БЕРИТЕ ЕДУ</li>
                    <li className="tag tag--primary">можно с животными</li>
                    <li className="tag tag--primary">Можно курить </li>
                </ul>
            </div>

            <div className="event__description">
                <h4 className="subtitle">Описание события:</h4>
                
                <p className="description__text">Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.</p>
            </div>

            <div className="event__demands">
                <h4 className="subtitle">Требования для входа от организатора:</h4>

                <ul className="tag__list">
                    <li className="tag tag--secondary">ДЕВУШКАМ БЕСПЛАТНО</li>
                    <li className="tag tag--secondary">ЕДА, АЛкоголь</li>
                    <li className="tag tag--secondary">300 рублей</li>
                </ul>
            </div>

            <button className="btn btn_2 event__go_to">Перейти в приложение</button>
        </div>
    )
}

export default EventDetails