import './banners.scss'
import img from '../../img/content/banner2.svg'

const BannerSecond = () => {
    return (
        <div className="banner banner--second">
            <div className="container">
                <div className="banner__content">
                    <img src={img} alt="" className="banner__img"/>
                </div>
            </div>
        </div>
    )
}

export default BannerSecond