import './banners.scss'
import BannerText from './bannerText'
import img from '../../img/content/banner3.svg'

const BannerThird = () => {
    return (
        <div className="banner banner--third">
            <div className="container">
                <div className="banner__content">
                    <BannerText title="Поехали веселиться вместе!!"/>

                    <img src={img} alt="" className="banner__img"/>
                </div>
            </div>
        </div>
    )
}

export default BannerThird